@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&family=Roboto:wght@300;400;500;700;900&display=swap');*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.main{
  width: 100%;
  height: 100vh;
  background-color: rgb(5, 38, 63);
}

.content{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  border-radius: 10px;
}

.top{
  width: 400px;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.city_input{
  width: 250px;
  height: 40px;
  outline: none;
  border: none;
  padding-left: 5px;
  font-size: 20px;
}

.search_btn{
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background-color: rgb(220, 243, 254);
  border: none;
  font-size: 17px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.search_btn:hover{
  background-color: rgb(5, 38, 63);
  color: #fff;
}

.icon-lg{
  font-size: 30px;
}


.bottom{
  width: 400px;
  min-height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 1;
  transition: opacity 300ms ease-in-out;
}
.weather_img{
  width: 150px;
  margin: auto;
}

.temp{
  font-size: 45px;
  font-weight: 700;
  color: rgb(5, 38, 63);
}

.weather_text{
  font-size: 20px;
  font-weight: 500;
  font-family: 'Roboto', sans-serif;
  padding: 5px;
}

.hum_wind{
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  padding: 50px;
}

.wind_main, .hum_main{
  display: flex;
  align-items: center;
}

.hw_icon{
  font-size: 30px;
  margin-right: 5px;

}

.hw_text{
  font-size: 16px;
  font-weight: 500;
  display: flex;
  flex-direction: column;
}

.hw_text1{
  font-size: 12px;
  font-weight: 600;

}

